import React, { Component } from 'react';

import { LineChart, Line, CartesianGrid, XAxis, YAxis, Tooltip  } from 'recharts';

import { makeStyles } from '@material-ui/core/styles';
import Modal from '@material-ui/core/Modal';

import { lodashToggle } from '../utils';


export function genData (data) {
  let dataParams = []
  data.map(record => {
    if (record.record && record.record.insidetemp )
      dataParams = lodashToggle(dataParams, {ts: tsToDateTime(record.ts), темп: record.record.insidetemp})
  })

  return dataParams
}


function rand() {
  return Math.round(Math.random() * 20) - 10;
}

function getModalStyle() {
  const top = 50 + rand();
  const left = 50 + rand();

  return {
    top: `${top}%`,
    left: `${left}%`,
    transform: `translate(-${top}%, -${left}%)`,
  };
}

const useStyles = makeStyles((theme) => ({
  paper: {
    position: 'absolute',
    // width: 400,
    backgroundColor: theme.palette.background.paper,
    border: '2px solid #000',
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3),
  },
}));

function ChartBody(props) {
  const classes = useStyles();
  const [modalStyle] = React.useState(getModalStyle);

  return (
    <div style={modalStyle} className={classes.paper}>
      <div className='card card-style my-1 mx-1'>
        <div className='content'>
          <div className='d-flex justify-content-start'>
            <h4 className='mr-5'>{props.label}</h4>
            {/* <h5 onClick={() => this.props.setTab({name: 'mainTab'})}>назад</h5> */}
          </div>

          <RenderLineChart data={props.data}/>
        </div>
      </div>
    </div>
  );
}

export class ChartModal extends Component {
  constructor(props) {
   super(props);

   this.state = {
    //  eng: false,
   }
 }

 componentDidMount() {
    let today = new Date();
    let yesterday = today.setDate(today.getDate() - 1);
    // this.props.getESystemsDataDetail({ws: 'pumps', t1: yesterday, t2: today})
    this.props.getESystemsDataDetail(this.props.query)
 }

 render () {
   const { detailDataList } = this.props
   const data = genData(detailDataList)
  //  const classes = useStyles();

   return (
    <ChartBody data={data} label={this.props.label}/>
   )
 }
}


export function RenderLineChart (props) {
  const { data } = props
  return (
    <LineChart width={600} height={300} data={data}>
      <Line type="monotone" dataKey="темп" stroke="#BF263C" />
      <CartesianGrid stroke="#ccc" />
      <XAxis dataKey="ts" />
      <YAxis />
      <Tooltip />
    </LineChart>
  )
}

 export function tsToDateTime(ts) {
    let date = new Date(ts);
    let dd = String(date.getDate()).padStart(2, '0');
    let mm = String(date.getMonth() + 1).padStart(2, '0');
    let yyyy = date.getFullYear();
    return `${dd}/${mm}T${date.getHours()}:${date.getMinutes()}`
}